import React, { useState } from 'react';
import Sidebar from './Sidebar';  // Import Sidebar
import styles from '../styles/Header.module.css';

const Header = () => {
  const [isSidebarCollapsed, setIsSidebarCollapsed] = useState(false);

  const toggleSidebarState = (collapsed) => {
    setIsSidebarCollapsed(collapsed); // Adjust header based on sidebar state
  };

  return (
    <div className={`${styles.header} ${isSidebarCollapsed ? styles.navbarCollapsed : ''}`}>
      <div className={styles.navbar}>
        <Sidebar toggleSidebarState={toggleSidebarState} />
        <div className={styles.navContent}>
          <div className={styles.search}>
            <input type="text" placeholder="Search..." />
          </div>
          <div className={styles.icons}>
            {/* You can add your icons here */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
